
.App {
  display: grid;
  grid-template-columns: 1fr 300px; 
  grid-template-rows: auto 1fr auto;
  grid-template-areas:
      "header header"
      "content sidebar"
      "footer sidebar";
  gap: 0px; 
  width: 100%;
  min-height: 100vh; 
}


body {
  box-sizing: border-box;
  background-color: #111;
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: white;
}

.content {
  grid-area: content; 
  background: #111;
  color: white;
  padding: 1rem;

  border-radius: 5px;
  margin: 0;
  width: auto; 
  height: auto; 
  overflow-y: auto; 
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .App {
      grid-template-columns: 1fr; 
      grid-template-rows: auto auto auto auto; 
      grid-template-areas:
          "header"
          "content"
          "sidebar"
          "footer";
  }

  .sidebar {
      width: 100%; 
      margin: 1rem 0; 
      position: relative; 
  }

  .content {
      width: 100%; 
  }

  .project-grid {
    grid-template-columns: 1fr; 
}
}