.sidebar {
    grid-area: sidebar; 
    background: #333;
    padding: 1.5rem; 
    height: auto; 
    width: 100%; 
    overflow-y: auto;
    position: relative; 
  }



.skills {
    padding-bottom: 5px;
}

a[href^="mailto"] {
    text-decoration: underline;
}

.skills ul {
    padding-left: 10px;
    list-style: none;
}

.skills ul > li {
    text-decoration:underline;
}

.skills li {
    margin-top: 10px;
}

.contact-info a {
    color: #ff6347;
}

.contact-info p,
a {
    padding-left: 10px;
}